import Logo from '../../components/UI/Logo/Logo';
import styles from './Secret.module.scss'
import Bubble from '../../components/Bubble/Bubble';
import TextBorder, { ThemeTextBorder } from '../../components/TextBorder/TextBorder';
import Button, { ThemeButton } from '../../components/UI/Button/Button';
import classNames from 'classnames';
import NavBar from '../../components/NavBarLayout/NavBarLayout';
import { useNavigate } from 'react-router-dom';


const Secret = () => {

  const navigate = useNavigate()

  const onSignUp = () => {
    navigate('/signup')
  }

  return (
    <>
      <div className={styles.bg}>
        <div className={styles.screen}>
          <Logo subtitle />

          <div>
            <TextBorder text='  ' center theme={ThemeTextBorder.GREENBLUE} className={styles.title} outlineClass={styles.title_outline} />

            <Bubble className={styles.info_bubble}>
              <p className={styles.info_text}>
                <span className={styles.info_bold}>   : </span>      !    ,  .
              </p>

              <p className={styles.info_text}>
                <span className={styles.info_bold}>  : </span> , ,   .
              </p>

              <p className={styles.info_text}>
                <span className={styles.info_bold}> :*   </span>
              </p>

              <div className={styles.recommendations}>
                <p className={classNames(styles.info_text, styles.recommendations_text)}>
                  1.     
                </p>

                <Button text='' theme={ThemeButton.RED} className={styles.btn} outlineClass={styles.btn_outline} onClick={onSignUp} />
              </div>

              <div className={styles.recommendations}>
                <p className={classNames(styles.info_text, styles.recommendations_text)}>
                  2.   
                </p>

                <Button text='' theme={ThemeButton.RED} className={styles.btn} outlineClass={styles.btn_outline} onClick={onSignUp} />
              </div>

              <div className={styles.recommendations}>
                <p className={classNames(styles.info_text, styles.recommendations_text)}>
                  3.     
                </p>

                <Button text='' theme={ThemeButton.RED} className={styles.btn} outlineClass={styles.btn_outline} onClick={onSignUp} />
              </div>

            </Bubble>

            <p className={styles.warning}>*   ,   </p>

          </div>
          <div className={styles.block}></div>
        </div>
      </div>
    </>
  )
};

export default Secret
