import TextBorder, { ThemeTextBorder } from '../../components/TextBorder/TextBorder';
import Button, { ThemeButton } from '../../components/UI/Button/Button';
import Logo from '../../components/UI/Logo/Logo';
import styles from './Result.module.scss'
import paris from '../../assets/images/results/paris.png'
import Bubble from '../../components/Bubble/Bubble';
import Modal from '../../components/UI/Modal/Modal';
import { useState, useEffect, useCallback } from 'react';
import { useNavigate } from 'react-router-dom';

const Result = () => {
  const [modal, setModal] = useState<boolean>(false)
  const navigate = useNavigate()

  const onClose = useCallback(() => {
    setModal(false)
  }, [])

  useEffect(() => {

    const onBack = () => {
      if (modal) {
        onClose()
      }
    }

    window.addEventListener("popstate", onBack, false);

    return () => window.removeEventListener("popstate", onBack, false);
  }, [navigate, modal, onClose])



  const onOpen = useCallback(() => {
    setModal(true)
    window.history.pushState({}, '', null);
  }, [])

  const onNext = useCallback(() => {
    navigate('/secret')
  }, [navigate])


  return (
    <>
      <Modal isOpen={modal} onClose={onClose}>
        <TextBorder text=' ' center theme={ThemeTextBorder.GREENBLUE} outlineClass={styles.modal_title_outline} className={styles.modal_title} />
        <div className={styles.modal_btns}>
          <Button theme={ThemeButton.BLUE} text=' ' className={styles.modal_btn} />
          <Button theme={ThemeButton.BLUE} text=' ' className={styles.modal_btn} />
        </div>
      </Modal>

      <div className={styles.bg}>
        <div className={styles.screen}>
          <Logo subtitle />
          <div>
            <TextBorder text='  ' center theme={ThemeTextBorder.GREENBLUE} className={styles.title} outlineClass={styles.title_outline} />
            <div className={styles.img_block}>
              <img alt='result' src={paris} className={styles.img} />
              <div className={styles.name_block}>
                <TextBorder text='' theme={ThemeTextBorder.GREENBLUE} className={styles.name} />
              </div>
            </div>
            <Bubble className={styles.info_bubble}>
              <p className={styles.info}>                .     ,       .
                            .</p>
            </Bubble>
          </div>
          <div className={styles.btns}>
            <Button theme={ThemeButton.BLUE} text='' onClick={onOpen} />
            <Button theme={ThemeButton.RED} text='   ' onClick={onNext} />
          </div>
        </div>
      </div>
    </>
  )
};

export default Result
