import { Iquestion } from './../../types/interface';
import { createSlice } from "@reduxjs/toolkit";


const namespace = 'test'
const questions: Iquestion[] = [
  {
    question: ' ',
    first: true
  },
  {
    question: ' ',
    options: [{ text: '', value: 1 }, { text: '', value: 2 }]
  },
  {
    question: '    ?',
    options: [{ text: '', value: 1 }, { text: '', value: 2 }]
  },
  {
    question: '   ?',
    options: [{ text: '', value: 1 }, { text: '', value: 2 }]
  },
  {
    question: '   ?',
    options: [{ text: '   ', value: 1 }, { text: ' 2   ', value: 2 }, { text: ' 1   ', value: 3 }, { text: '  2   ', value: 4 }]
  },
  {
    question: '  ?',
    options: [{ text: ' ', value: 1 }, { text: ' ', value: 2 }, { text: ' ', value: 3 }]
  },
  {
    question: '   ?',
    options: [{ text: '', value: 1 }, { text: '', value: 2 }]
  },
  {
    question: '      ?',
    options: [{ text: ' 20:00', value: 1 }, { text: ' 19:00', value: 2 }, { text: ' 19:00', value: 3 }]
  },
  {
    question: '     ?',
    options: [{ text: '3  ', value: 1 }, { text: '', value: 2 }, { text: ' , ', value: 3 }, { text: '', value: 4 }]
  },
  {
    question: '      ? ',
    options: [{ text: '  ', value: 1 }, { text: ' ', value: 2 }, { text: '  ', value: 3 }]
  },
  {
    question: '  ?',
    options: [{ text: '', value: 1 }, { text: '', value: 2 }, { text: ' ', value: 3 }],
  },
  {
    question: '  ?',
    options: [{ text: ',   ', value: 1 }, { text: '   30 ', value: 2 }, { text: '  10 ', value: 3 }]
  },
  {
    question: '      ?',
    options: [{ text: '8 ', value: 1 }, { text: '10 ', value: 2 }, { text: '12 ', value: 3 }]
  },
]


export interface ITest {
  questions: Iquestion[]
  step: number
}


const initialState = {
  questions: questions,
  step: 0
}


export const testSlice = createSlice({
  name: namespace,
  initialState: initialState,
  reducers: {
    plusStep: (state) => {
      if (state.questions.length - 1 === state.step) return
      state.step += 1;
    },
    minusStep: (state) => {
      if (state.questions.length === 0) return
      state.step -= 1;
    },
    resetSteps: (state) => {
      state.step = 0
    }
  },
})

export const { plusStep, minusStep, resetSteps } = testSlice.actions;